html,
body {
	&.tutor-component-popup-opened {
		overflow: hidden !important;
	}
}

.tutor-component-popup-container {
	padding: 15px;
	overflow: hidden;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 9999999999;
	background: rgba(0, 0, 0, 0.589);

	@include valueWithPrefix(display, flex);
	@include propertyWithPrefix(align-items, center);
	@include propertyWithPrefix(justify-content, center);

	& > div {
		display: inline-block;

		max-height: 100%;
		width: auto;
		max-width: 100%;

		background: white;
		overflow: auto;
		border-radius: 15px;
		position: relative;
		padding: 0;

		&.tutor-component-popup-40 {
			padding: 40px;
		}

		img.tutor-pop-icon {
			display: block;
			margin: 0 auto;
		}

		h3 {
			margin: 7px 0 30px 0;
			font-style: normal;
			font-weight: normal;
			font-size: 30px;
			text-align: center;
			color: $black-main;
			line-height: 32px;
		}

		p {
			margin: 5px 0 30px;
			text-align: center;
			max-width: 560px;
			word-wrap: break-word;
		}

		.tutor-component-button-container {
			text-align: center;
			button {
				margin: 5px;
			}
		}
	}
}

.tutor-component-three-col-action {
	@include breakpoint-min(tablet) {
		@include valueWithPrefix(display, flex);
		@include propertyWithPrefix(align-items, center);
	}
	border: 1px solid #dcdbdc;
	border-radius: 6px;
	padding: 25px 35px 25px 30px;
	margin-bottom: 15px;

	& > *:first-child {
		margin-right: 15px;
	}

	& > div:nth-child(2) {
		@include propertyWithPrefix(flex, 1);

		small {
			font-size: 16px;
		}

		p {
			font-size: 20px;
			line-height: 26px;
			margin: 0;
		}
	}
}
